import { __decorate } from "tslib";
import { Vue, Component, Mixins, Watch } from 'vue-property-decorator';
import { featuresMapper } from '@/modules/features/module/store';
import ListFeatures from '@/modules/features/components/ListFeatures.vue';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import MsgBoxTools from '@/modules/features/mixins/MsgBoxTools';
import MessageBox from '@/components/MessageBox.vue';
import animateIfVisible from '@/mixins/animateIfVisible';
import { authMapper } from '@/modules/auth/module/store';
const FeaturesMappers = Vue.extend({
    computed: {
        ...featuresMapper.mapState(['list']),
        ...featuresMapper.mapGetters(['isLoading'])
    },
    methods: {
        ...featuresMapper.mapActions(['getList']),
    }
});
const AuthMappers = Vue.extend({
    computed: {
        ...authMapper.mapGetters(['activeMenuSectionByLink'])
    }
});
let ModuleFeatures = class ModuleFeatures extends Mixins(FeaturesMappers, AuthMappers, MsgBoxToolsApp, MsgBoxTools) {
    constructor() {
        super(...arguments);
        this.secondBtn = { type: 'success', isPlain: true };
    }
    get link() { return this.$route && this.$route.matched && this.$route.matched[0].path.slice(1); }
    get activeSection() { return this.link && this.activeMenuSectionByLink(this.link); }
    async onListChange(val) {
        if (val.data && val.data.length) {
            await this.$nextTick();
            animateIfVisible();
        }
    }
    created() {
        this.updateList();
    }
    goToPageApp() { this.$router.push({ path: '/' }); }
    onFirstBtnClick() {
        if (this.msgBoxIsShown)
            this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failFetchList':
                this.updateList();
                break;
        }
    }
    updateList() {
        if (this.list.isLoading)
            return;
        this.getList()
            .catch((err) => {
            if (err && err.status && err.status.toString().slice(0, 2) == 40)
                this.$emit('goToPageAuth');
            else {
                this.requestStatus = 'failFetchList';
                this.openMsgBox();
            }
        });
    }
};
__decorate([
    Watch('list', { deep: true })
], ModuleFeatures.prototype, "onListChange", null);
ModuleFeatures = __decorate([
    Component({
        components: {
            ListFeatures,
            MessageBox
        }
    })
], ModuleFeatures);
export default ModuleFeatures;
